//Desktop
@media (min-width: #{map-get($grid-breakpoints, xl)}) {
	//mobile
.it-list-wrapper{
	.it-list{
		a{
			// hover
			&:hover{
				box-shadow: $card-shadow;
				color: $primary-a5;
				text-decoration: none;
				background: #FFF;
				position: relative;
				z-index: 1;
				transition:none;
				.it-right-zone{
					border-bottom: 1px solid transparent;
				}
				span.text{
					text-decoration: underline;
				}
			}
		}
	}
}

